// inputs include AAMP.getAvailableAudioTracks and AAMP.getAudioTrackInfo
const englishLanguageStrings = [
    'en',
    'eng',
    'und',
];

export const audioTrackMapFunction = ({accessibility, codec, language, name, rendition}) => ({
    codec,
    description: name,
    language: (englishLanguageStrings.includes(language)) ? 'eng' : language,
    role: rendition,
    accessibilityType: (accessibility) ? 'description' : null,
});

// inputs include AAMP.getAvailableTextTracks and AAMP.getTextTrackInfo
export const textTrackMapFunction = ({codec, language, name, rendition, type}) => ({
    codec,
    description: name,
    language: (englishLanguageStrings.includes(language)) ? 'eng' : language,
    role: rendition,
    type,
});
