/* eslint-disable no-console */
import {name as appName, version as appVersion} from '../../package.json'; // eslint-disable-line import/no-unused-modules
import {MSG_TYPES, sendApplicationServicesMessage} from '../utils/as-messaging';
import {changeIframeSource} from '../utils/iframe';
import {IFRAME_MANAGER_URL_PATH} from '../utils/constants';

import Player from './player';
import ppApiHandler from './pp-handler';
import {setupDeviceUuid} from './system-token-storage';

window.isConsoleEnabled = window.localStorage.getItem('CONSOLE_ENABLED') || false;
if (!window.isConsoleEnabled) {
    console.warn('Integrated Player/JSPP console logging is partially disabled. To enable all console log: navigate to port 10000 and send command `localStorage.setItem("CONSOLE_ENABLED", "true")` then `location.reload()`');
    for (const logFunction of ['log', 'info']) {
        // eslint-disable-next-line no-console, no-empty-function
        console[logFunction] = () => {};
    }
}

/**
 * Main execution thread of the application.  Here is where we keep lifecycle such as user data.
 *
 * @typedef {import('../utils/as-messaging').WebSocketMessageConfigure} WebSocketMessageConfigure
 *
 * @param {Object}                      main                         object containing params for main thread
 * @param {WebSocket}                   main.webSocket               websocket to be passed to main or new version
 * @param {WebSocketMessageConfigure}   main.glassConfigureObject    object from AS 'configure' websocket message, contains mac address and details of environment
 * @param {Object}                      main.partnerConfig           Configuration Object from remote config that contains overrides and other settings/feature flags, whatever.
 * @param {Object}                      main.configureRequestPayload configure request payload from initial websocket connection, require for reply.
 */
export default async function mainApplication({
    webSocket,
    glassConfigureObject = {},
    partnerConfig = {},
}) {
    const {partnerId, estbMacAddress} = glassConfigureObject;
    const mountEl$ = document.querySelector('#mount') || document.createElement('div');
    const iframeManager$ = document.querySelector('#streamotionGlassIframeManager');

    // Preload our iframe manager to use default no-provider (magneto/foxtel)
    changeIframeSource(iframeManager$, `${IFRAME_MANAGER_URL_PATH}?partnerId=${partnerId}&estbMacAddress=${estbMacAddress}&provider=`);

    const player = new Player({
        initConfigOverrides: {},
    });

    console.log(`AAMP version ${player.version} created`);

    console.info(`${appName} version ${appVersion} running main thread`); // eslint-disable-line no-console
    console.log(`OS ${glassConfigureObject.osVersion}\nconfigure`, {glassConfigureObject, partnerConfig}); // eslint-disable-line no-console

    const uuid = await setupDeviceUuid();

    if (process.env.NODE_ENV === 'development') {
        mountEl$.innerHTML = 'Press <a style="text-shadow: 2px 2px #91c742";>Home</a> and select an asset.';
        mountEl$.style.display = 'block';
    }

    webSocket.onclose = function onClose(event) {
        if (event.wasClean) {
            console.log('WebSocket connection closed cleanly', {event}); // eslint-disable-line no-console
        } else {
            console.error('WebSocket connection died', {event});
        }
    };

    webSocket.onerror = function onError(error) {
        console.error('WebSocketError', error);
    };

    webSocket.onmessage = function onMessage(event) {
        const request = JSON.parse(event.data);
        const {msgType} = request;

        const logFunction = (request.msgData?.fnName === 'setAsset' || request.msgData?.fnName === 'stop')
            ? 'debug' : 'log';

        console[logFunction](`WebSocket request received from AS to main ${JSON.stringify(request)}`); // eslint-disable-line no-console

        switch (msgType) {
            case MSG_TYPES.GET_STATE:
                sendApplicationServicesMessage('getStateReturn', {state: 'ready'}, webSocket, request);
                break;

            case MSG_TYPES.PP_API_REGISTER_ALL:
                player.setCallbackOnEvents((eventName, payload) => {
                    sendApplicationServicesMessage(
                        'ppApiEvent',
                        {
                            eventName,
                            eventData: {
                                type: eventName,
                                ...payload,
                            },
                        },
                        webSocket,
                        request
                    );
                });

                // no response message required for ppRegisterAll
                break;

            case MSG_TYPES.PP_API_CALL:
                ppApiHandler({
                    request,
                    player,
                    deviceInfo: {
                        appName: 'magneto-integrated-player',
                        appVersion,
                        glassConfigure: glassConfigureObject,
                        playerName: player.name,
                        playerVersion: player.version,
                        uuid,
                    },
                    webSocket,

                    iframeManager$,
                });
                break;

            default:
                console.warn('msgType not supported ', request);
                break;
        }
    };
}

