import {name as appName, version as appVersion} from '../package.json'; // eslint-disable-line import/no-unused-modules
import {getPartnerIdConfig, getGlassIntegratedPlayerScriptUrlFromVersion} from './utils/configs';
import {addProxyIp} from './utils/utils';
import {MSG_TYPES, sendApplicationServicesMessage} from './utils/as-messaging';
import defaultMainApplication from './app/main';

// Always print the running script name + version.
console.info(`${appName} version ${appVersion} loaded`); // eslint-disable-line no-console

// Development only hot reloading;  WIP to save us from the window reloading each time, would be nice to reconnect to websocket.
if (process.env.NODE_ENV === 'development') {
    if (module.hot) {
        module.hot.dispose(function hotReloadDispose(data) {
            // module is about to be replaced.
            // You can save data that should be accessible to the new asset in `data`
            console.error('dispose');
            data.updated = Date.now();
        });

        module.hot.accept(function hotReloadAccept(/* getParents */) {
            console.error('accept');

            // module or one of its dependencies was just updated.
            // data stored in `dispose` is available in `module.hot.data`
            const {updated} = module.hot.data;

            console.log('RELOADED', updated); // eslint-disable-line no-console
            window.location.reload(true);
        });
    }
}

export default function init({
    preloadedWebSocket = undefined,
    preloadedGlassConfigureObject = undefined,
    preloadedPartnerGlassConfigureObject = undefined,
} = {}) {
    const webSocket = preloadedWebSocket || new WebSocket('ws://localhost:9031');

    if (preloadedGlassConfigureObject) {
        defaultMainApplication({
            webSocket,
            glassConfigureObject: preloadedGlassConfigureObject,
            partnerConfig: preloadedPartnerGlassConfigureObject,
        });
    } else {
        webSocket.onopen = function onOpen(/* event */) {
            console.log('WebSocket connection established'); // eslint-disable-line no-console
        };

        webSocket.onmessage = function onMessage(event) {
            const request = JSON.parse(event.data);
            const {msgType, msgData} = request;

            const logFunction = (msgType === MSG_TYPES.CONFIGURE) ? 'debug' : 'log';

            console[logFunction](`WebSocket request received from AS to index ${JSON.stringify(request)}`); // eslint-disable-line no-console

            if (msgType === MSG_TYPES.CONFIGURE) {
                // if `configure` doesn't have partnerId, ignore it
                if (msgData.accountId && msgData.estbMacAddress && msgData.partnerId) {
                    // regardless if we use it or not, we're going to fetch remote config.
                    // Remote config in the future may grow to have some other options for "prior-to-booting" things.
                    loadLatestVersionFromGlassConfigureObject(webSocket, msgData, defaultMainApplication);
                } else {
                    console.warn('AS \'configure\' message ignored, waiting for another containing accountId, estbMacAddress and partnerId');
                }
            } else if (msgType === MSG_TYPES.GET_STATE) {
                // While we're still loading things, this reply keeps further instructions away from us.
                // We may wish to use further instructions from a differently loaded bundle.
                sendApplicationServicesMessage(
                    MSG_TYPES.GET_STATE_RETURN,
                    {state: 'loadingPpConfig'},
                    webSocket,
                    request
                );
            } else {
                console.warn('Loader: Previous WebSocket message not handled prior to loader finishing', msgType, msgData);
            }
        };
    }
}

// We expose this on the dom, so if another version is calling us, it can call us.
window.streamotionGlass = {init, version: appVersion};

/**
 * Fetches config on resources matching the device's partnerId.  If there, and finds version override,
 * loads that script and passes configuration to the new app's init function.
 * If any error, or no config or same version, hit our callback that should reference our app's main function.
 *
 * @typedef {import('./utils/as-messaging').WebSocketMessageConfigure} WebSocketMessageConfigure
 *
 * @param {WebSocket}               webSocket               websocket to be passed to main or new version
 * @param {WebSocketMessageConfigure} glassConfigureObject       Configuration Object from websocket that contains mac address and details of environment
 * @param {requestCallback}         callbackIfNoOverrides   Callback to hit if no different script to load.
 */
export async function loadLatestVersionFromGlassConfigureObject(webSocket, glassConfigureObject, callbackIfNoOverrides) {
    const {partnerId} = glassConfigureObject;
    const partnerConfigUrl = getPartnerIdConfig(partnerId);

    let overrideVersion;

    fetch(addProxyIp(partnerConfigUrl))
        .then((response) => {
            if (response.ok) {
                return response.json();
            } else {
                throw `Unable to get partner config. ${partnerConfigUrl} ${response.status} ${response.statusText}`;
            }
        })
        .then((partnerConfig) => {
            // If development build, never try for a different version.  Use local main.
            if (process.env.NODE_ENV === 'development') {
                return callbackIfNoOverrides({webSocket, glassConfigureObject, partnerConfig});
            }

            // Do we have a version to use from config?
            if (partnerConfig?.version) {
                overrideVersion = partnerConfig.version;
            }

            // Do we have a version to use from MAC address overrides?  This more pressing than global version.
            if (partnerConfig?.macAddressOverrides?.[glassConfigureObject.estbMacAddress]) {
                overrideVersion = partnerConfig.macAddressOverrides[glassConfigureObject.estbMacAddress];
            }

            // If we don't have an override version, or we do and it's the same to the one loaded right now, just use local main.
            if (!overrideVersion || overrideVersion === appVersion) {
                return callbackIfNoOverrides({webSocket, glassConfigureObject, partnerConfig});
            } else {
                // Loading override version script from predefined location.
                return new Promise((resolve, reject) => {
                    const script = document.createElement('script');

                    script.src = getGlassIntegratedPlayerScriptUrlFromVersion(overrideVersion);
                    script.type = 'module';
                    script.addEventListener('load', resolve);
                    script.addEventListener('error', (e) => reject(e));
                    document.head.appendChild(script);
                })
                    .then(() => {
                        // When successful, pass all our currently open things to the new version of ourselves exposed on the dom (overwrites)
                        window.streamotionGlass.init({
                            preloadedWebSocket: webSocket,
                            preloadedGlassConfigureObject: glassConfigureObject,
                            preloadedPartnerGlassConfigureObject: partnerConfig,
                        });
                    })
                    .catch((error) => {
                        // Any error loading the new script, pass to our local main.
                        console.error('loadLatestVersionFromGlassConfigureObject', error);

                        callbackIfNoOverrides({webSocket, glassConfigureObject, partnerConfig});
                    });
            }
        })
        .catch((error) => {
            console.error('loadLatestVersionFromGlassConfigureObject Error in Fetch Partner Config', error);

            callbackIfNoOverrides({webSocket, glassConfigureObject});
        });
}
