const PROXY_IP = window.localStorage.getItem('PROXY_IP') || '';

export function addProxyIp(url, proxyIp = PROXY_IP) {
    if (proxyIp && url && !url.includes(proxyIp)) {
        return `${proxyIp}${url}`;
    }

    return url;
}

export function getVideoPositionFromContentPosition(contentPositionMilliseconds, adBreaks) {
    let videoPositionMilliseconds = contentPositionMilliseconds;

    for (const adBreak of adBreaks) {
        for (const ad of adBreak.ads) {
            if (ad.startTime < videoPositionMilliseconds) {
                videoPositionMilliseconds += ad.duration;
            // } else if (ad.startTime === videoPositionMilliseconds) {
            //     console.log('start of an ad');
            }
        }
    }

    return videoPositionMilliseconds;
}

