import {addProxyIp} from './utils';

/**
 * Use New Relic's Log API to capture a message
 * New Relic Log API https://docs.newrelic.com/docs/logs/log-api/introduction-log-api/
 * Example NRQL query `SELECT * FROM Log WHERE logtype = 'jspp' SINCE 24 hours ago`
 *
 * @param {String} message main log message
 * @param {Object} attributes other fields that may be useful
 */
export default async function sendNewRelicLog(message, attributes) {
    const requestBody = {
        hostname: 'jspp.resources.streamotion.com.au',
        logtype: 'jspp',
        message,
        timestamp: new Date().toISOString(),
        ...attributes,
    };

    fetch(
        addProxyIp('https://log-api.newrelic.com/log/v1'),
        {
            method: 'POST',
            headers: {
                'Api-Key': 'f476a78d10f213e60031a460a952d58dFFFFNRAL',
                'Content-Type': 'application/json',
            },
            body: JSON.stringify(requestBody),
        }
    ).catch((error) => {
        console.error('New Relic log failed to send', error);
    });
}
