/* eslint-disable no-console */
import {sendApplicationServicesMessage} from '../utils/as-messaging';
import IntegratedPlayerError from '../utils/integrated-player-error';
import getProviderAsset from './get-asset';

export default async function ppApiHandler({
    player,
    request,
    deviceInfo,
    webSocket,

    iframeManager$,
}) {
    const {msgData} = request;
    const ppFunctionName = msgData.fnName;
    const ppApiReturnReplyFunction = getPpApiReturnReplyFunction(webSocket, ppFunctionName, request);

    let result = false;

    switch (ppFunctionName) {
        case 'getAvailableAudioTracks':
            ppApiReturnReplyFunction(
                player.getAvailableAudioTracks()
            );
            break;

        case 'getAvailableTextTracks':
            ppApiReturnReplyFunction(
                player.getAvailableTextTracks()
            );
            break;

        case 'getCurrentAudioTrack':
            ppApiReturnReplyFunction(
                player.getCurrentAudioTrack()
            );
            break;

        case 'getCurrentTextTrack':
            ppApiReturnReplyFunction(
                player.getCurrentTextTrack()
            );
            break;

        case 'getTimeline':
            // eslint-disable-next-line no-case-declarations
            const timeline = player.getTimeline(ppApiReturnReplyFunction);

            if (timeline) {
                ppApiReturnReplyFunction(
                    timeline
                );
            }

            break;

        case 'pause':
            if (player.pause()) {
                ppApiReturnReplyFunction(true);
            } else {
                ppApiReturnReplyFunction(-301);
            }

            break;

        case 'setAsset':
            player.setIsEnabled(true);

            document.querySelector('#mount').style.display = 'none';
            ppApiReturnReplyFunction(true); // AS messaging quirk, if this line is performed after setSources AS doesn't send the pause message
            if (window.mediaFailedTimeout) {
                clearTimeout(window.mediaFailedTimeout);
            }

            try {
                iframeManager$.contentWindow.provider?.monitoringTeardown();
                if (!msgData.fnArgs[0].match(/^https?:\/\/magneto\.com\//gm)) {
                    throw `invalid locator ${msgData.fnArgs[0]}`;
                }

                const [locator, contentOptions] = msgData.fnArgs;

                player.newRelicLogFields = {
                    deviceInfo,
                    provider: new URL(locator).searchParams.get('provider') || 'magneto',
                    setAsset: {
                        locator,
                        contentOptions,
                    },
                };

                const providerAsset = await getProviderAsset({
                    msgData,
                    deviceInfo,
                    iframeManager$,
                });

                if (!player.isEnabled) {
                    console.log('stop received, ignore this request MIPA-182');
                    return;
                }

                iframeManager$.contentWindow.provider.setVideoAdBreaksCallback = (videoAdBreaks, error = null) => {
                    if (error) {
                        player.fireMediaFailed(new IntegratedPlayerError({
                            major: 20000,
                            minor: 103,
                            desc: 'adTrackingUrl failed to return',
                        },
                        {response: error}));
                        player.reportError({
                            code: '20000|103',
                            description: `adTrackingUrl failed to return: ${error}`,
                        });
                        return;
                    }

                    // eslint-disable-next-line no-console
                    console.info(`setVideoAdBreaksCallback() ${JSON.stringify(videoAdBreaks)}`);
                    player.videoAdBreaks = videoAdBreaks;

                    // stream can now start playback (or buffer then start)
                    player.play();
                };

                iframeManager$.contentWindow.provider.monitoringSetup(player.videoElement);

                player.setSources(
                    providerAsset.streams,
                    msgData.fnArgs[1],
                    providerAsset?.aampInitConfigOverrides,
                    providerAsset.recoverySettings
                );
            } catch (e) {
                let error = e;

                if (!e.getMediaFailedPayload) {
                    console.error('uncaught error', e);
                    error = new IntegratedPlayerError({major: 20000, minor: 100, desc: `uncaught error: ${e}`},
                        {
                            response: e,
                            responseString: `${e}`,
                        });
                }

                // report error to AS
                if (msgData?.fnArgs[1]?.assetType === 'LINEAR' && error.getMediaFailedPayload()?.error?.major === 20000) {
                    console.log('MIPA-165 delaying error', error);
                    // eslint-disable-next-line require-atomic-updates
                    window.mediaFailedTimeout = setTimeout(() => {
                        console.log('MIPA-165 firing delayed error', error);
                        player.fireMediaFailed(error);
                    }, 2000);
                } else {
                    player.fireMediaFailed(error);
                }
            }
            break;

        case 'setBlock':
            ppApiReturnReplyFunction(
                player.setBlock(msgData.fnArgs[0])
            );
            break;

        case 'setDimensionsOfVideo':
            ppApiReturnReplyFunction(true);
            break;

        case 'setPosition':
            result = player.setPosition(msgData.fnArgs[0]);
            if (result === player.trickplayResponseEnum.Success) {
                ppApiReturnReplyFunction(true);
            } else if (result === player.trickplayResponseEnum.BlockedByAd) {
                player.callbackOnEvents('MediaWarning', {
                    error: {
                        major: 70,
                        minor: null,
                        description: 'seek not allowed by current segment',
                        isWarning: true,
                    },
                });
            } else if (result === player.trickplayResponseEnum.TrickplayDisabled) {
                ppApiReturnReplyFunction(-302);
            } else {
                console.warn('unexpected response to setSpeed');
            }

            break;

        case 'setPreferredAudioLanguage':
            ppApiReturnReplyFunction(
                player.setPreferredAudioLanguage(msgData.fnArgs)
            );
            break;

        case 'setSpeed':
            result = player.setSpeed(msgData.fnArgs[0]);
            if (result === player.trickplayResponseEnum.Success) {
                ppApiReturnReplyFunction(true);
            } else if (result === player.trickplayResponseEnum.BlockedByAd) {
                player.callbackOnEvents('MediaWarning', {
                    error: {
                        major: 70,
                        minor: null,
                        description: 'speed change not allowed by current segment',
                        isWarning: true,
                    },
                });
            } else if (result === player.trickplayResponseEnum.TrickplayDisabled) {
                ppApiReturnReplyFunction(-301);
            } else {
                console.warn('unexpected response to setSpeed');
            }

            break;

        case 'setTextTrack':
            ppApiReturnReplyFunction(
                player.setTextTrack(msgData.fnArgs)
            );
            break;

        case 'setTextTrackEnabled':
            ppApiReturnReplyFunction(
                player.setTextTrackEnabled(msgData.fnArgs[0])
            );
            break;

        case 'setVolume':
            ppApiReturnReplyFunction(
                player.setVolume(msgData.fnArgs[0] * 100)
            );
            break;

        case 'stop':
            ppApiReturnReplyFunction(
                player.stop()
            );
            iframeManager$.contentWindow.provider?.monitoringTeardown();
            break;

        case 'updateSessionActivity':
            // updateSessionActivity message can be ignored MIPA-271
            break;

        default:
            console.warn(`ppApiHandler ppFunctionName ${ppFunctionName} not supported, msgData=${JSON.stringify(msgData)}`);
    }
}

export function getPpApiReturnReplyFunction(webSocket, fnName, request) {
    return function ppApiReturnReplyFunction(returnVal) {
        if (typeof returnVal == 'boolean') {
            // eslint-disable-next-line no-param-reassign
            returnVal = (returnVal === true) ? 0 : -1;
        }

        const payload = {
            returnVal,
        };

        sendApplicationServicesMessage(
            'ppApiReturn',
            payload,
            webSocket,
            request
        );
    };
}
