export const getPartnerIdConfig = (partnerId) => (`https://resources.streamotion.com.au/glass/integrated-player/partner-configs/${partnerId}.json`);

export const getGlassIntegratedPlayerScriptUrlFromVersion = (version) => {
    if (version.indexOf('-qa') > -1 || version.indexOf('-pr') > -1) {
        return `https://resources.streamotion.com.au/glass/integrated-player/builds/qa/${version}/dist/index.js`;
    } else {
        return `https://resources.streamotion.com.au/glass/integrated-player/builds/version/${version}/dist/index.js`;
    }
};

export const getGlassIntegratedPlayerIframeManagerScriptUrlFromVersion = (version) => {
    if (version.indexOf('-qa') > -1) {
        return `https://resources.streamotion.com.au/glass/integrated-player/builds/qa/${version}/dist/iframe-manager/index.js`;
    } else {
        return `https://resources.streamotion.com.au/glass/integrated-player/builds/version/${version}/dist/iframe-manager/index.js`;
    }
};
