/* eslint-disable no-bitwise */
import {v4 as generateUUID} from 'uuid';
import IntegratedPlayerError from '../utils/integrated-player-error';

export class Providers {
    static ABC = 'au.com.abc';
    static SBS = 'au.com.sbs';
    static NINE = 'au.com.nine';
    static SEVEN = 'au.com.seven';
    static TEN = 'au.com.ten';

    static UUID = 'au.com.device.uuid';
}

export async function getTokenForProvider(provider, glassAuthToken = null) {
    // first party provider gets glassAuthToken (XSCT)
    if (provider === null) {
        return glassAuthToken;
    }

    // third party provider fetches  from token storage
    return fetch(`http://localhost:9005/as/system/token/${provider}`)
        .then((response) => {
            if (response.ok && response.status === 200) {
                return response.json();
            } else {
                throw new IntegratedPlayerError({major: 20000, minor: 110, desc: 'no valid 3rd party authorization token'});
            }
        })
        .then((body) => {
            if (body.token) {
                return body.token;
            } else {
                throw new IntegratedPlayerError({major: 20000, minor: 110, desc: 'no valid 3rd party authorization token'});
            }
        });
}

async function setTokenForProvider(provider, token) {
    return fetch(`http://localhost:9005/as/system/token/${provider}`,
        {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({
                token,
            }),
        })
        .then((response) => {
            if (response.ok && response.status === 200) {
                return token;
            } else {
                throw new IntegratedPlayerError({desc: '3rd party token could not be set'});
            }
        });
}

export async function setupDeviceUuid() {
    try {
        return await getTokenForProvider(Providers.UUID);
    } catch {
        const token = await setTokenForProvider(Providers.UUID, generateUUID())
            .catch((error) => {
                console.error('could not set Device UUID', error);
            });

        // eslint-disable-next-line no-console
        console.info('Device UUID created', token);

        return token;
    }
}
